import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { CommonStorage } from '../services';
import { Location } from '@angular/common';
@Component({
  selector: 'app-ourbus-contactus',
  templateUrl: './ourbus-contactus.page.html',
  styleUrls: ['./ourbus-contactus.page.scss'],
})
export class OurbusContactusPage implements OnInit {
  helpLineNumber: any;
  operatorEmail: any;

  constructor(
    private navCtrl:NavController,
    private commonStorage: CommonStorage,
    private location: Location
  ) { }

  ngOnInit() {
    this.helpLineNumber = this.commonStorage.getItem('metaData').helpLineNumber.split(',');
    this.operatorEmail = this.commonStorage.getItem('metaData').operatorEmail;
  }
  goToContactSupport(){
    this.navCtrl.navigateForward('contact-us');
  }

  handleBackButton() {
    
    if (window.history.length > 1) {
      this.location.back();
    } else {
      this.navCtrl.navigateRoot('tabs/home');
    }
  }

}
